/*
Interactive
===========
*/
/*
Add the correct display in Chrome and Safari.
*/
summary {
  display: list-item;
}

ul {
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 1.75rem;
  position: relative;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.5rem;
}

.actions {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
}
.actions button {
  padding: 0.5rem 1rem;
  font-size: 2.5rem;
}

.lesson__note {
  position: absolute;
  bottom: -40rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  outline: 0.25rem groove hsl(189, 13%, 6%);
  font-size: 1.5rem;
  width: 80vw;
}
.account__displays {
  font-size: 1.5rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  gap: 0.75rem;
}

.transfer {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1.25rem;
}
.transfer button {
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
}
.transfer input {
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  flex: 1;
  border-radius: 0.25rem;
  border: 1px solid;
}
.transfer.hidden {
  display: none;
}