@use "functions"as *;
// @use "colors"as *;

@mixin min-w($w:0, $p:100%) {
    width: min($w, $p);
}

;

@mixin max-w($w:0, $p:100%) {
    width: max($w, $p);
}

;

@mixin bp-max($bp:0) {
    @media (max-width: $bp) {
        @content
    }
}

;

@mixin bp-min($bp:0) {
    @media (min-width: $bp) {
        @content
    }
}

;

@mixin equal($h-w:0) {
    height: $h-w;
    width: $h-w;
}

;

@mixin flex($d:row, $a:center, $j:center) {
    display: flex;
    flex-direction: $d;
    align-items: $a;
    justify-content: $j;
}

;

@mixin equalGrid($value:0) {
    display: grid;
    grid-template-columns: repeat($value, 1fr);
    grid-template-rows: repeat($value, 1fr);
}

;

@mixin debug() {
    outline: .25rem groove random-dark-color();
}

;

@mixin responsiveGrid() {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(16rem, 1fr));
    grid-template-rows: auto;
}

;

@mixin scrollBar() {
    &::-webkit-scrollbar {
        width: .25rem;
    }

    &::-webkit-scrollbar-track {
        background: $white-100;
    }

    &::-webkit-scrollbar-thumb {
        background: rgba($color: $red-300, $alpha: 0.125);
        border-radius: .5rem;
    }

    &::-webkit-scrollbar-thumb:hover {
        background: #555;
    }
}