@use "."as *;

html {
    font-size: 62.5%;
}

body {
    @include flex;
    padding: map-get($map: $space, $key: lg);
    position: relative;
}

.container {
    @include flex(column);
    gap: map-get($map: $space, $key: md) + .25rem;
}

.actions {
    @include flex();
    gap: map-get($map: $space, $key: md);


    button {
        padding: .5rem 1rem;
        font-size: 2.5rem;
    }
}

.lesson__note {
    position: absolute;
    bottom: -40rem;
    @include flex(column);
    @include debug;
    font-size: 1.5rem;
    width: 80vw;

    @include bp-max(map-get($breakpoints, sm)) {
        // display: none;
    }
}

.account {
    &__displays {
        font-size: 1.5rem;
        width: 100%;
        @include flex(column, flex-start);
        gap: map-get($map: $space, $key: sm);
    }
}

.transfer {
    width: 100%;
    @include flex;
    gap: map-get($map: $space, $key: md);

    button {
        padding: .5rem 1rem;
        font-size: 1.5rem;
    }

    input {
        font-size: 1.5rem;
        padding: .5rem 1rem;
        flex: 1;
        border-radius: map-get($map: $radius, $key: xs);
        border: 1px solid;
    }

    &.hidden {
        display: none;
    }
}